<template>
  <div class="slide-slider">
    <h2 class="slider-title" v-if="activeLang == 'en'">News Feed</h2>
    <h2 class="slider-title" v-if="activeLang == 'ar'">آخر الآخبار</h2>
    <div
      class="scroll-view"
      @mousedown="handleMouseDown"
      @mouseup="handleMouseUp"
      @mousemove="handleMouseMove"
    >
      <button
        class="forward fa"
        :class="
          direction === 'rtl'
            ? 'fa-chevron-left forward-dir-rtl'
            : 'fa-chevron-right'
        "
        @click="toForward()"
        :disabled="clickCounter <= 0 ? 'disabled' : null"
      />
      <button
        class="backward fa"
        :class="
          direction === 'rtl'
            ? 'fa-chevron-right backward-dir-rtl'
            : 'fa-chevron-left'
        "
        @click="toBackward()"
        :disabled="clickCounter >= lastCount ? 'disabled' : null"
      />
      <div
        class="h-scroll"
        :style="
          direction === 'ltr'
            ? { left: this.leftValue + '%' }
            : { right: this.leftValue + '%' }
        "
      >
        <div
          v-for="(item, index) in data"
          :key="item.id"
          class="item-container"
        >
          <div class="item" :class="index === activeItem ? 'active' : null">
            <div class="heading">
              <h5 class="author">{{ item.author }}</h5>
              <h2 class="title">{{ item.title }}</h2>
            </div>
            <div class="content">
              <h5 class="author">{{ item.author }}</h5>
              <h2
                class="title"
                :style="
                  direction === 'ltr'
                    ? { left: '25px' }
                    : { right: '25px', left: 'auto' }
                "
              >
                {{ item.title }}
              </h2>
              <p class="desc">{{ item.desc }}</p>
              <button class="read-more">
                {{ activeLang === "en" ? "Read more" : "للمزيد" }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import axios from "axios";
export default {
  name: "SlideSlider",
  computed: mapGetters(["activeLang", "direction"]),
  data() {
    return {
      scrollView: null,
      data: [],
      lastCount: 0,
      leftValue: 0,
      clickCounter: 0,
      length: 0,
      itemsCount: 3,
      shift: 33.33,
      dragged: false,
      mouseX: 0,
      pageX: 0,
      activeItem: 1
    };
  },
  computed: mapGetters(["update", "activeLang", "direction"]),
  updated() {
    if (this.update) {
      this.fetchData();
      this.adjustScreen();
    }
  },
  created() {
    this.fetchData();
    this.adjustScreen();
    window.addEventListener("resize", () => {
      this.adjustScreen();
    });
  },
  methods: {
    async fetchData() {
      let response = null;
      if (this.activeLang == "en") {
        response = await axios.get("json/slideSlider.json");
      } else if (this.activeLang == "ar") {
        response = await axios.get("json/slideSlider_ar.json");
      }
      this.data = response.data;
      this.length = this.data.length;
    },
    adjustScreen: function() {
      if (window.innerWidth >= 768) {
        this.lastCount = this.length - this.itemsCount;
        this.shift = 33.33;
        this.activeItem = 1;
      } else if (window.innerWidth < 768) {
        this.lastCount = this.length - 1;
        this.shift = 100;
        this.activeItem = 0;
      }
    },
    toForward: function() {
      if (this.clickCounter > 0) {
        this.leftValue = this.leftValue + this.shift;
        this.clickCounter = this.clickCounter - 1;
        this.activeItem = this.activeItem - 1;
      }
    },
    toBackward: function() {
      if (this.clickCounter < this.lastCount) {
        this.leftValue = this.leftValue - this.shift;
        this.clickCounter = this.clickCounter + 1;
        this.activeItem = this.activeItem + 1;
      }
    },
    handleMouseDown: function(e) {
      this.scrollView.style.cursor = "grabbing";
      this.dragged = true;
      this.mouseX = e.pageX;
    },
    handleMouseUp: function() {
      this.scrollView.style.cursor = "grab";
      this.dragged = false;
      this.mouseX = 0;
    },
    handleMouseMove: function(e) {
      this.pageX = e.pageX;
      if (this.dragged === true) {
        if (this.pageX <= this.mouseX) {
          this.toBackward();
        } else {
          this.toForward();
        }
        this.dragged = false;
      }
    }
  },
  mounted() {
    const element = document.querySelector(".scroll-view");
    this.scrollView = element;
  }
};
</script>

<style lang="scss" scoped>
@import "../general-settings";
$slide-time: 0.7s;
.slide-slider {
  width: 100%;
  height: 500px;
  padding: 25px;
  margin-bottom: 100px;
  .slider-title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 25px;
  }
  .scroll-view {
    overflow: hidden;
    position: relative;
    cursor: grab;
  }
  .forward,
  .backward {
    position: absolute;
    top: 40%;
    transform: translateY(-50%);
    z-index: 10;
    box-shadow: none !important;
    font-size: 2rem;
    width: 45px;
    height: 45px;
    outline: transparent;
    border: 1px solid;
  }
  .forward:not([disabled]):hover,
  .backward:not([disabled]):hover {
    box-shadow: 1px 1px 4px #777 !important;
    cursor: pointer;
  }
  .forward {
    right: 0;
  }
  .backward {
    left: 0;
  }
  .forward-dir-rtl {
    left: 0;
    right: auto;
  }
  .backward-dir-rtl {
    right: 0;
    left: auto;
  }
  .h-scroll {
    position: relative;
    display: flex;
    transition: all $slide-time ease-in-out;
    .item-container {
      min-width: 33.33%;
      padding: 25px;
      transition: all 0.7s;
      .item {
        height: 250px;
        background: rgba(45, 45, 45, 0.7);
        padding: 10px;
        color: #fff;
        position: relative;
        transition: all 0.5s;
        .heading {
          .title {
            position: absolute;
            top: 150px;
            font-size: 1.4rem;
            font-weight: 200;
          }
          .author {
            font-size: 0.9rem;
            font-weight: 100;
            padding: 10px 0;
          }
        }
        .content {
          transform: scale(0);
        }
      }
    }
    .item-container {
      .active {
        height: auto;
        margin-top: -25px;
        .heading {
          display: none;
        }
        .content {
          transform: scale(1);
          background: $main-accessories-color;
          padding: 15px;
          .title {
            padding: 20px 0;
          }
          .author {
            padding: 10px 0;
          }
          .desc {
            padding: 5px 0;
            line-height: 1.5rem;
            font-size: 1rem;
          }
          .read-more {
            margin-top: 5px;
            padding: 10px 25px;
            outline: transparent;
            background: transparent;
            border: 1px solid;
            color: #fff;
            cursor: pointer;
            transition: all 0.2s;
            &:hover {
              color: $main-accessories-color;
              background: #fff;
            }
          }
        }
      }
    }
  }
}
/*=====================================================
============= Medium && Small Screen ========
===========================================*/
@media (max-width: 768px) {
  .slide-slider {
    .h-scroll {
      .item-container {
        min-width: 100%;
      }
    }
  }
}
</style>
