<template>
  <div id="scrollToTop" class="scroll-to-top">
    <i class="fa fa-chevron-circle-up" @click="scrollToTop()"></i>
  </div>
</template>

<script>
export default {
  name: "scrollToTop",
  mounted() {
    const scrollBtn = document.getElementById("scrollToTop");
    document.addEventListener("scroll", function() {
      if (window.scrollY > 500) {
        scrollBtn.style.opacity = 1;
        scrollBtn.style.visibility = "visible";
      } else {
        scrollBtn.style.opacity = 0;
        scrollBtn.style.visibility = "hidden";
      }
    });
  },
  methods: {
    scrollToTop: function() {
      window.scrollTo(0, 0);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../general-settings";
.scroll-to-top {
  position: fixed;
  right: 10px;
  bottom: 10px;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  color: $main-accessories-color;
  cursor: pointer;
  visibility: hidden;
  transition: all 0.7s;
  z-index: 111;
  &:hover {
    i {
      color: black;
    }
  }
}
</style>
