import axios from "axios";

const state = {
  sliderData: [],
  activeSlide: 0,
  slideTime: 10000
};
const getters = {
  sliderData: state => state.sliderData,
  activeSlide: state => state.activeSlide
};
let sliderTimer;
const actions = {
  startSlider: ({ dispatch }) => {
    dispatch("getSliderData");
    dispatch("automaticSlider");
  },
  stopSlider: () => {
    clearInterval(sliderTimer);
  },
  getSliderData: async ({ rootState, commit }) => {
    let result;
    if (rootState.lang.activeLang === "en")
      result = await axios.get("json/sliderData.json");
    if (rootState.lang.activeLang === "ar")
      result = await axios.get("json/sliderData_ar.json");
    commit("setSliderData", result);
  },
  automaticSlider: ({ state, commit, dispatch }) => {
    sliderTimer = setInterval(function() {
      if (window.scrollY < 800 && window.location.pathname === "/") {
        let index = state.activeSlide + 1;
        if (index === state.sliderData.data.length) {
          index = 0;
        }
        dispatch("animateText");
        dispatch("flashing", 100);
        commit("setActiveSlide", index);
      }
    }, state.slideTime);
  },
  chooseSlide: ({ state, commit, dispatch }, slide) => {
    const index = state.sliderData.data.findIndex(i => slide === i);
    dispatch("animateText");
    dispatch("flashing", 100);
    commit("setActiveSlide", index);
    dispatch("stopSlider");
  },
  nextSlide: ({ state, commit, dispatch }) => {
    const index = state.activeSlide + 1;
    const newIndex =
      index !== state.sliderData.data.length ? state.activeSlide + 1 : 0;
    dispatch("animateText");
    dispatch("flashing", 100);
    commit("setActiveSlide", newIndex);
    dispatch("stopSlider");
  },
  prevSlide: ({ state, commit, dispatch }) => {
    const index = state.activeSlide - 1;
    const newIndex =
      index !== -1 ? state.activeSlide - 1 : state.sliderData.data.length - 1;
    dispatch("animateText");
    dispatch("flashing", 100);
    commit("setActiveSlide", newIndex);
    dispatch("stopSlider");
  },
  flashing: ({ state }, time) => {
    const main = document.getElementById("mainImg");
    if (main !== null) {
      const index = main.className.indexOf("flashEffect");
      if (index === -1) {
        const classString = main.className;
        const newClass = classString.concat(" flashEffect");
        main.className = newClass;
        setTimeout(function() {
          const index = main.className.indexOf("flashEffect");
          const newClass = main.className.substring(0, index - 1);
          main.className = newClass;
        }, (state.slideTime * time) / state.slideTime);
      }
    }
  },
  animateText: () => {
    const heading = document.getElementById("titles");
    if (heading !== null) {
      const index = heading.className.indexOf("animate-text");
      if (index === -1) {
        const classString = heading.className;
        const newClass = classString.concat(" animate-text");
        heading.className = newClass;
        setTimeout(function() {
          const index = heading.className.indexOf("animate-text");
          const newClass = heading.className.substring(0, index - 1);
          heading.className = newClass;
        }, state.slideTime / 2);
      } else {
        const newClass = heading.className.substring(0, index - 1);
        heading.className = newClass;
        setTimeout(function() {
          const classString = heading.className;
          const newClass = classString.concat(" animate-text");
          heading.className = newClass;
        }, 50);
      }
    }
  }
};
const mutations = {
  setSliderData: (state, result) => (state.sliderData = result),
  setActiveSlide: (state, index) => (state.activeSlide = index)
};

export default {
  state,
  getters,
  actions,
  mutations
};
