<template>
  <div
    class="lang-button"
    :class="direction === 'ltr' ? 'adjust-left' : 'adjust-right'"
  >
    <button
      :class="activeLang == 'en' ? 'active' : ''"
      @click="changeActiveLang('en')"
    >
      EN
    </button>
    <button
      class="rtl-button"
      :class="activeLang == 'ar' ? 'active' : ''"
      @click="changeActiveLang('ar')"
    >
      ع
    </button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "LangButton",
  computed: mapGetters(["direction", "activeLang"]),
  methods: mapActions(["changeActiveLang"])
};
</script>

<style lang="scss" scoped>
.lang-button {
  position: fixed;
  top: 5px;
  z-index: 1111111;
  button {
    display: block;
    color: #eee;
    width: 25px;
    height: 25px;
    margin: 1px;
    background: transparent;
    border: 1px solid;
    cursor: pointer;
    outline: none !important;
  }
  button.active {
    background: #eee;
    color: #333;
    transform: scale(0.85);
    font-weight: 700;
  }
  button:hover {
    background: #fff;
    color: darkorange;
  }
  .rtl-button {
    background: #333;
  }
}
.adjust-left {
  left: 5px;
}
.adjust-right {
  right: 5px;
  left: auto;
}
@media (max-width: 992px) {
  .lang-button {
    top: 90px;
  }
  .adjust-left {
    left: 0;
  }
  .adjust-right {
    right: 0;
    left: auto;
  }
}
@media (max-width: 768px) {
  .lang-button {
    top: 65px;
  }
}
</style>
