<template>
  <section class="tabs" v-if="tabs.length > 0">
    <h3 class="title" v-if="activeLang === 'en'">Our Work</h3>
    <h3 class="title" v-if="activeLang === 'ar'">المشاريع</h3>
    <ul class="buttons">
      <li
        class="tab-btn"
        @click="handleClick('all')"
        :class="activeTab == 'all' ? ' active' : ''"
      >
        {{ activeLang === "en" ? "All" : "الجميع" }}
      </li>
      <li
        class="tab-btn"
        v-for="tab in tabs_buttons"
        :key="tab"
        @click="handleClick(tab)"
        :class="activeTab == tab ? ' active' : ''"
      >
        {{ tab }}
      </li>
    </ul>
    <ul class="images">
      <li class="tab-image" v-for="tab in filtered_tabs" :key="tab.id">
        <img :src="tab.img_src" alt="tab image" />
        <div class="content">
          <h4 class="content-link">
            <a href="portfolio-details.html">{{ tab.link }}</a>
          </h4>
          <h6 class="content-tab">{{ tab.title }}</h6>
        </div>
      </li>
    </ul>
  </section>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "Tabs",
  data() {
    return {
      tabs: [],
      filtered_tabs: [],
      tabs_buttons: [],
      activeTab: "all"
    };
  },
  computed: mapGetters(["update", "activeLang", "direction"]),
  updated() {
    if (this.update) {
      this.fetchData();
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      let response = null;
      if (this.activeLang == "en") {
        response = await axios.get("json/tabs.json");
      } else if (this.activeLang == "ar") {
        response = await axios.get("json/tabs_ar.json");
      }
      if (response) {
        this.tabs = response.data;
        // this.filtered_tabs = response.data;
        this.handleClick(this.activeTab);
        const filteredTabs = new Set();
        this.tabs.forEach(item => filteredTabs.add(item.title));
        this.tabs_buttons = filteredTabs;
      }
    },
    handleClick(tab) {
      this.activeTab = tab;
      if (tab === "all") {
        this.filtered_tabs = this.tabs;
      } else {
        this.filtered_tabs = this.tabs.filter(item => item.title === tab);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../general-settings";
$active_color: $main-accessories-color;
.tabs {
  padding: 25px 0;
  .title {
    font-size: 2.5rem;
    text-align: center;
  }
  .buttons {
    display: flex;
    justify-content: center;
    list-style: none;
    margin: 25px 0;
    .tab-btn {
      margin: 5px;
      padding: 5px 15px;
      font-size: 1.2rem;
      text-transform: capitalize;
      cursor: pointer;
      position: relative;
      &::before,
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 0;
        height: 0;
        transition: all 0.5s;
        box-sizing: border-box;
        border-radius: 2px;
      }
      &::before {
        top: 0;
        left: 0;
        border-top: 1px solid $active_color;
        border-left: 1px solid $active_color;
      }
      &::after {
        bottom: 0;
        right: 0;
        border-bottom: 1px solid $active_color;
        border-right: 1px solid $active_color;
      }
      &:hover {
        color: $active_color;
      }
      &:hover::after,
      &:hover::before {
        width: 100%;
        height: 100%;
      }
    }
    .active {
      color: $active_color;
      transform: translateY(2px);
    }
  }
  .images {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    .tab-image {
      width: 25%;
      animation: appear 0.5s 1;
      transition: all 0.5s;
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .content {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: all 0.4s;
      overflow: hidden;
      .content-link {
        z-index: 2;
        transition: all 0.4s;
        transform: translateY(100px);
        a {
          color: white;
          font-size: 1.6rem;
          text-decoration: none;
        }
        &:hover {
          a {
            color: $main-accessories-color;
          }
        }
      }
      .content-tab {
        color: white;
        font-size: 1.4rem;
        z-index: 2;
        font-weight: 200;
        margin-top: 25px;
        transition: all 0.4s;
        transform: translateY(-100px);
      }
      &:before {
        content: "";
        display: block;
        position: absolute;
        top: -100%;
        left: 0;
        width: 100%;
        height: 50%;
        background: rgba(255, 255, 255, 0.7);
        transition: all 0.2s 0.5s;
        z-index: -1;
      }
      &:hover {
        box-shadow: inset 0 0 5px 15px rgba(0, 0, 0, 0.5);
        opacity: 1;
        &:before {
          top: 100%;
          z-index: 1;
        }
        &:after {
          content: "";
          position: absolute;
          height: calc(100% - 20px);
          width: calc(100% - 20px);
          background: rgba(0, 0, 0, 0.7);
          z-index: 1;
        }
        .content-link {
          transform: translateY(0);
        }
        .content-tab {
          transform: translateY(0);
        }
      }
    }
    @keyframes appear {
      0% {
        transform: scale(0);
      }
      100% {
        transform: scale(1);
      }
    }
  }
}
/*==========================================================================
============ Medium Screen =================
===========================================*/
@media (max-width: 768px) {
  .tabs {
    .images {
      .tab-image {
        width: 50%;
      }
    }
  }
}
/*==========================================================================
============ Small Screen =================
===========================================*/
@media (max-width: 576px) {
  .tabs {
    padding: 50px 0;
    .title {
      font-size: 2rem;
    }
    .buttons {
      margin: 5px 0;
      .tab-btn {
        margin: 5px;
        padding: 5px;
        font-size: 1rem;
      }
    }
    .images {
      .content {
        .content-link {
          a {
            font-size: 1.2rem;
          }
        }
        .content-tab {
          font-size: 1rem;
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .tabs {
    .images {
      .tab-image {
        width: 100%;
      }
    }
  }
}
</style>
