<template>
  <section class="what-we-do">
    <div class="col-mid promo-image">
      <img src="/images/home/what-we-do.jpg" alt="promo image" />
    </div>
    <div class="col-mid">
      <div class="content-container">
        <h2 class="content-title left" v-if="activeLang == 'en'">What we do</h2>
        <h2 class="content-title right" v-if="activeLang == 'ar'">
          ماذا نقدّم ؟
        </h2>
        <div class="services">
          <div class="service" v-for="task in tasks" :key="task.id">
            <div
              class="service-icon"
              :style="
                direction === 'ltr'
                  ? { marginRight: '30px' }
                  : { marginRight: 0, marginLeft: '30px' }
              "
            >
              <i :class="`fa ${task.icon}`"></i>
            </div>
            <div
              class="service-content"
              :style="
                direction === 'ltr'
                  ? { textAlign: 'left' }
                  : { textAlign: 'right' }
              "
            >
              <h4>{{ task.title }}</h4>
              <p>
                {{ task.desc }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "What-We-Do",
  data() {
    return {
      tasks: []
    };
  },
  methods: {
    async fetchData() {
      let response = null;
      if (this.activeLang == "en") {
        response = await axios.get("json/what-we-do.json");
      } else if (this.activeLang == "ar") {
        response = await axios.get("json/what-we-do_ar.json");
      }
      this.tasks = response.data;
    }
  },
  computed: mapGetters(["update", "activeLang", "direction"]),
  updated() {
    if (this.update) {
      this.fetchData();
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

<style lang="scss" scoped>
@import "../general-settings";
.what-we-do {
  display: flex;
  .col-mid {
    width: 50%;
  }
  .promo-image {
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      animation: zoomInOut 3s infinite alternate linear;
    }
    @keyframes zoomInOut {
      0% {
        transform: scale(1);
      }
      100% {
        transform: scale(1.2);
      }
    }
  }
  .content-container {
    padding: 60px 40px;
    .content-title {
      font-family: "Poppins", sans-serif;
      font-size: 2.5rem;
      position: relative;
      margin-bottom: 40px;
      padding-bottom: 15px;
      color: #333;
      &:after {
        content: "";
        display: block;
        width: 20%;
        height: 2px;
        background: #333;
        opacity: 0.4;
        position: absolute;
        bottom: 0;
      }
    }
    .left {
      &:after {
        left: 0;
      }
    }
    .right {
      &:after {
        left: auto;
        right: 0;
      }
    }
    .services {
      .service {
        display: flex;
        margin-top: 40px;
        .service-icon {
          height: 50px;
          width: 50px;
          border: 1px solid #dddddd;
          text-align: center;
          color: $main-accessories-color;
          font-size: 20px;
          line-height: 50px;
          position: relative;
          &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
          }
        }
        .service-content {
          flex: 1;
          margin-top: -5px;
        }
        &:hover {
          .service-icon:before {
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
            animation: bounce 0.6s 1;
          }
          @keyframes bounce {
            0% {
              transform: scale(1);
              opacity: 1;
            }
            100% {
              transform: scale(1.5);
              opacity: 0.1;
            }
          }
        }
      }
    }
  }
}
/*==========================================================================
============ Medium Screen && Small Screen =================
===========================================*/
@media (max-width: 960px) {
  .what-we-do {
    flex-direction: column-reverse;
    .col-mid {
      width: 100%;
    }
  }
}
</style>
