const state = {
  update: true,
  updateQuotes: 1,
  direction: "ltr",
  activeLang: "en"
};
const getters = {
  direction: state => state.direction,
  activeLang: state => state.activeLang,
  update: state => state.update,
  updateQuotes: state => state.updateQuotes
};
const actions = {
  changeActiveLang: ({ commit }, lang) => commit("changeActiveLang", lang)
};
const mutations = {
  changeActiveLang: (state, lang) => {
    state.update = true;
    state.updateQuotes = 0;
    if (lang == "en") {
      state.activeLang = "en";
      state.direction = "ltr";
    }
    if (lang == "ar") {
      state.activeLang = "ar";
      state.direction = "rtl";
    }
  },
  stopUpdate: state => (state.update = false),
  stopUpdateQuotes: state => (state.updateQuotes = 1)
};

export default {
  state,
  getters,
  actions,
  mutations
};
