<template>
  <section class="skills" id="skills">
    <h2 class="title" v-if="activeLang === 'en'">Our Skills</h2>
    <h2 class="title" v-if="activeLang === 'ar'">المهارات</h2>

    <div class="skills-container">
      <div class="col-mid">
        <div class="skill-image">
          <img src="/images/home/skills.jpg" alt="skills author" />
        </div>
      </div>
      <div class="col-mid skills-area">
        <div v-for="skill in skills" :key="skill.id" class="skill">
          <span class="progress-icon">
            <i class="fa" :class="skill.icon"></i>
          </span>
          <div
            class="progress-wrap"
            :style="
              direction === 'ltr'
                ? { paddingRight: '50px' }
                : { paddingRight: 0, paddingLeft: '50px' }
            "
          >
            <h4>{{ skill.title }}</h4>
            <div class="progress">
              <div
                class="progress-bar"
                :class="direction === 'ltr' ? 'adjust-left' : 'adjust-right'"
                :style="animateBar ? { width: skill.value + '%' } : null"
              ></div>
            </div>
            <span
              class="progress-count"
              :style="
                direction === 'ltr'
                  ? { right: 0, textAlign: 'right' }
                  : { left: 0, right: 'auto', textAlign: 'left' }
              "
            >
              <span class="counter">{{ skill.value }}</span
              >%</span
            >
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "Skills",
  data() {
    return {
      skills: [],
      animateBar: false
    };
  },
  methods: {
    async fetchData() {
      let response = null;
      if (this.activeLang == "en") {
        response = await axios.get("json/skills.json");
      } else if (this.activeLang == "ar") {
        response = await axios.get("json/skills_ar.json");
      }
      this.skills = response.data;
    }
  },
  computed: mapGetters(["update", "activeLang", "direction"]),
  updated() {
    if (this.update) {
      this.fetchData();
    }
  },
  created() {
    this.fetchData();
  },
  mounted() {
    const skills = document.getElementById("skills");
    document.addEventListener("scroll", () => {
      if (window.pageYOffset >= skills.offsetTop - 250) {
        this.animateBar = true;
      } else {
        this.animateBar = false;
      }
    });
  }
};
</script>
<style lang="scss" scoped>
@import "../general-settings";
.skills {
  padding: 30px 85px;
  margin-top: 25px;
  .title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 25px;
  }
  .skills-container {
    display: flex;
    .col-mid {
      width: 50%;
    }
    .skill-image {
      img {
        width: 100%;
      }
    }
    .skills-area {
      min-height: 100%;
      padding: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      box-shadow: 0 0 20px 20px rgba(158, 158, 158, 0.1);
      .skill {
        display: flex;
        align-items: flex-end;
        padding: 15px 0;
        .progress-icon {
          background: #f6f6f6;
          color: #999999;
          height: 50px;
          width: 50px;
          font-size: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 5px 10px 5px 5px;
        }
        .progress-wrap {
          overflow: hidden;
          padding-bottom: 10px;
          width: 100%;
          flex-shrink: 100;
          align-self: center;
          position: relative;
          .progress {
            background: #c2c2c2;
            height: 3px;
            overflow: visible;
            display: flex;
            font-size: 0.75rem;
            border-radius: 0.25rem;
            .progress-bar {
              position: relative;
              height: 1px;
              margin-top: 1px;
              transition: all 2.5s;
              background-color: #007bff;
              width: 0;
              &:after {
                content: "";
                position: absolute;
                top: 0;
                height: 14px;
                width: 14px;
                margin-left: -7px;
                margin-top: -7px;
                border-radius: 50%;
                background: rgba(42, 104, 252, 0.9);
                border: 1px solid #c2c2c2;
                z-index: 1;
              }
            }
            .adjust-left {
              &:after {
                right: auto;
                left: 100%;
              }
            }
            .adjust-right {
              &:after {
                left: auto;
                right: 100%;
              }
            }
          }
          .progress-count {
            width: 50px;
            font-weight: 700;
            font-size: 16px;
            position: absolute;
            top: 3px;
            color: $main-accessories-color;
          }
        }
      }
    }
  }
}
/*==========================================================================
============ Medium Screen =================
===========================================*/
@media (max-width: 960px) {
  .skills {
    padding: 15px 45px;
    margin-top: 25px;
    .title {
      font-size: 1.75rem;
    }
    .skills-container {
      flex-direction: column;
      .col-mid {
        width: 100%;
      }
    }
  }
}
/*==========================================================================
============ Small Screen =================
===========================================*/
@media (max-width: 576px) {
  .skills {
    padding: 5px 20px;
    margin-top: 25px;
    .title {
      font-size: 1.5rem;
    }
  }
}
</style>
