<template>
  <div class="counter-item">
    <h4 class="title">{{ items }}</h4>
    <div class="counter-circular-container" :class="items">
      <div class="outer-border"></div>
      <div class="inner-border"></div>
      <div class="right-ring"></div>
      <div class="left-ring"></div>
      <div class="left-gray-ring"></div>
      <div class="right-gray-ring"></div>
      <h6 class="counter-value" :class="items" :data-items="count"></h6>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
/************************************************************/
/**** Counter with Changeable Count of Items ****************/
/************************************************************/
/*** Designed By Eng.Rawad Khair -2020 **********************/
export default {
  name: "CircularCounter",
  props: ["items", "count"],
  computed: mapGetters(["update"]),
  mounted() {
    this.StartCounter();
  },
  updated() {
    if (this.update) {
      this.StartCounter();
    }
  },
  methods: {
    StartCounter() {
      const separator = document.getElementById("separator");
      // const win = document.getElementsByTagName("body")[0];
      /**** Use whole Counter for Items *****/
      const counterValue = document.querySelector(
        `.counter-value.${this.items}`
      );
      const itemsCount = counterValue.getAttribute("data-items"); // Get Total Number of All items
      const counterStart = document.querySelector(
        `.counter-circular-container.${this.items}`
      );
      const itemsEach = 10,
        itemsEachTime = 25, //We count each {itemsEach} books every {itemsEachTime}
        counterTime = (itemsCount / itemsEach / 1000) * itemsEachTime;
      //************ Start Count When Scroll *************//
      let justOne = 0;

      document.addEventListener("scroll", () => {
        if (window.pageYOffset >= separator.offsetTop - 250) {
          justOne++;
          if (justOne === 1) {
            //This variable let counter animation declare only one time
            const classString = counterStart.className;
            const newClass = classString.concat(" start-counter");
            counterStart.className = newClass;
            animateCounter(counterValue, counterStart);
          }
        }
      });
      /**** Circular Counter Animations ************/
      function animateCounter(counterValue, counterStart) {
        /****** Animate Left Ring ********/
        const leftRing = counterStart.querySelector(".left-ring"); //Element
        const leftRingAnimation =
          "rotate-left 1 " + counterTime / 2 + "s" + " linear forwards"; //Animation
        setAnimate(leftRing, leftRingAnimation);
        /****** Animate Right Ring ********/
        const rightRing = counterStart.querySelector(".right-ring"); //Element
        const rightRingAnimation =
          "rotate-right 1 " +
          counterTime / 2 +
          "s " +
          counterTime / 2 +
          "s" +
          " linear forwards"; //Animation
        setAnimate(rightRing, rightRingAnimation);
        /****** Animate Font Size of Counter Value********/
        const fontAnimation =
          "fontExplode 1 " + counterTime + "s" + " linear forwards"; //Animation
        setAnimate(counterValue, fontAnimation);

        function setAnimate(element, animation) {
          element.style.animation = "none";
          element.style.animation = animation;
          ///****** Counter: Change value */
          let count = 0;
          const counter = setInterval(function() {
            counterValue.textContent = count;
            if (count >= itemsCount) {
              clearInterval(counter);
              counterValue.textContent = itemsCount;
            }
            count = count + itemsEach;
          }, itemsEachTime);
        }
      } //End Circular Counter Animations  ------------*/
    }
  }
};
</script>
<style lang="scss">
$dark-color: transparent;
$light-color: transparent;
$count-color: #fff;
$dimension: 160px;
$counter-width: 5px;
$counter-time: 2.5s;
$delay: 0s;
$ring-gray-color: transparent;
.counter-item {
  position: relative;
  .title {
    position: absolute;
    bottom: 25%;
    width: 100%;
    text-align: center;
    color: #fff;
    z-index: 5;
    text-transform: capitalize;
  }
  .counter-circular-container {
    width: $dimension * 1.1;
    height: $dimension * 1.1;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    margin: 25px;
    background-color: transparent;
    .counters-item-title {
      text-align: center;
      text-transform: capitalize;
    }
    .counter-value {
      display: none;
    }
    .outer-border,
    .inner-border {
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: 1px solid $dark-color;
      z-index: 9999;
    }
    .outer-border {
      width: $dimension;
      height: $dimension;
      box-shadow: 0px 0px 3px 1px $light-color;
    }
    .inner-border {
      width: $dimension - 2 * $counter-width;
      height: $dimension - 2 * $counter-width;
      box-shadow: inset 0px 0px 5px 8px $light-color;
    }
  }

  .start-counter {
    .left-ring {
      width: $dimension / 2;
      height: $dimension;
      border-left: $counter-width solid $dark-color;
      border-top: $counter-width solid $dark-color;
      border-bottom: $counter-width solid $dark-color;
      border-top-left-radius: 110px;
      border-bottom-left-radius: 110px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform-origin: 0 0;
      transform: translate(-100%, -50%);
      // animation: rotate-left 1 $counter-time/2 $delay linear forwards;
      z-index: 4;
    }
    @keyframes rotate-left {
      0% {
        transform: rotate(0deg) translate(-100%, -50%);
      }
      99% {
        z-index: 4;
      }
      100% {
        transform: rotate(180deg) translate(-100%, -50%);
        z-index: 7;
      }
    }
    .left-gray-ring {
      width: $dimension / 2;
      height: $dimension;
      border-left: $counter-width solid $ring-gray-color;
      border-top: $counter-width solid $ring-gray-color;
      border-bottom: $counter-width solid $ring-gray-color;
      border-top-left-radius: 110px;
      border-bottom-left-radius: 110px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-100%, -50%);
      z-index: 5;
    }

    .right-ring {
      width: $dimension / 2;
      height: $dimension;
      border-right: $counter-width solid $dark-color;
      border-top: $counter-width solid $dark-color;
      border-bottom: $counter-width solid $dark-color;
      border-top-right-radius: 110px;
      border-bottom-right-radius: 110px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(0%, -50%);
      transform-origin: 0 0;
      // animation: rotate-right 1 $counter-time/2 $counter-time/2 + $delay linear
      //   forwards;
      z-index: 1;
    }
    @keyframes rotate-right {
      0% {
        transform: rotate(0deg) translate(0%, -50%);
        z-index: 6;
      }
      100% {
        transform: rotate(180deg) translate(0%, -50%);
        z-index: 7;
      }
    }

    .right-gray-ring {
      width: $dimension / 2;
      height: $dimension;
      border-right: $counter-width solid $ring-gray-color;
      border-top: $counter-width solid $ring-gray-color;
      border-bottom: $counter-width solid $ring-gray-color;
      border-top-right-radius: 110px;
      border-bottom-right-radius: 110px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(0%, -50%);
      z-index: 2;
    }

    .counter-value {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: $count-color;
      // animation: fontExplode 1 $counter-time $delay linear forwards;
      display: block;
      font-family: fantasy;
      letter-spacing: 2px;
      z-index: 5;
    }
    @keyframes fontExplode {
      0% {
        font-size: 8px;
      }
      100% {
        font-size: 2rem;
      }
    }
  }
}
/*==========================================================================
============ Small Screen =================
===========================================*/
@media (max-width: 576px) {
  .counter-item {
    .counter-circular-container {
      width: $dimension / 2;
      height: $dimension / 2;
      margin: 25px;
    }
  }
}
</style>
