<template>
  <nav id="mainNavbar" class="main-navbar">
    <div class="navbar-container">
      <div class="navbar-brand">
        <router-link to="" class="navbar-brand-link">
          <h3 class="navbar-brand-title" v-if="activeLang === 'en'">logo</h3>
          <h3 class="navbar-brand-title" v-if="activeLang === 'ar'">اللوغو</h3>
        </router-link>
      </div>
      <ul class="navbar-list" :style="showList ? { transform: 'none' } : null">
        <li
          v-for="item in navbarItems"
          :key="item.id"
          class="navbar-list-item"
          :class="item.dropMenu ? 'navbar-item-has-dropmenu' : null"
        >
          <router-link exact :to="item.url" class="navbar-list-link">
            {{ item.title }}
          </router-link>
          <ul v-if="item.dropMenu" class="navbar-dropmenu-outer">
            <li v-for="subItem in item.dropMenu" :key="subItem.id">
              <router-link :to="subItem.url">
                {{ subItem.subTitle }}
              </router-link>
              <ul v-if="subItem.subDropMenu" class="navbar-dropmenu-inner">
                <li
                  v-for="secondSub in subItem.subDropMenu"
                  :key="secondSub.id"
                >
                  <router-link :to="secondSub.url">{{
                    secondSub.subTitle
                  }}</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </li>
      </ul>
      <div @click="showList = !showList" class="navbar-switch">
        <i :class="showList ? 'fa fa-times' : 'fa fa-navicon'"></i>
      </div>
      <LangButton></LangButton>
    </div>
  </nav>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import LangButton from "@/components/LangButton";
export default {
  name: "Navbar",
  data() {
    return {
      showList: false
    };
  },
  methods: {
    ...mapActions(["fetchNavbarItems"]),
    ...mapMutations(["stopUpdate"])
  },
  computed: mapGetters(["update", "navbarItems", "activeLang"]),
  components: { LangButton },
  created() {
    this.fetchNavbarItems();
  },
  updated() {
    if (this.update) {
      this.fetchNavbarItems();
      this.stopUpdate();
    }
  }
};
</script>

<style lang="scss" scoped>
$bg-color: transparent;
$fixed-bg-color: #333;
$border-color: #666666;
$dropMenu-bg-color: darkblue;
$color: #e2e2e2;
$mobile-color: #eee;
$height: 100px;
$padding: 100px;
$margin-between-links: 25px;
$font-size: 16px;
$font-weight: 400;
$font-family: sans-serif;
$subMenu-width: 250px;
$animation-time: 0.2s;
$animation-type: ease;

nav {
  font-family: $font-family;
  background-color: $bg-color;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  &.fixed-navbar {
    position: fixed;
    background: $fixed-bg-color;
    .navbar-container {
      height: $height * 0.75;
      .navbar-dropmenu-outer {
        top: $height * 0.75 !important;
      }
    }
    transform-origin: top center;
    animation: slideDown 0.4s 1;

    @keyframes slideDown {
      from {
        transform: scaleY(0);
      }
      to {
        trnasform: scaleY(1);
      }
    }
  }
  .navbar-container {
    height: $height;
    border-style: solid;
    border-width: 0 0 1px 0;
    border-color: $border-color;
    padding: 0 $padding;
    display: flex;
    justify-content: space-between;
    .navbar-brand {
      align-self: center;
      .navbar-brand-link {
        text-decoration: none;
      }
      .navbar-brand-title {
        font-size: 2rem;
        text-transform: uppercase;
        font-family: cursive;
        color: $color;
        text-shadow: 0 0 5px darkorange;
      }
    }
    .navbar-switch {
      display: none;
    }
    .navbar-list {
      list-style: none;
      display: flex;
      .navbar-list-item {
        margin: 0 $margin-between-links;
        display: flex;
        flex-direction: column;
        justify-content: center;
        cursor: pointer;
        .navbar-list-link {
          text-decoration: none;
          text-transform: capitalize;
          font-size: $font-size;
          font-weight: $font-weight;
          color: $color;
          position: relative;
        }
        .navbar-list-link:after {
          content: "";
          display: block;
          width: 0;
          height: 1px;
          position: absolute;
          bottom: -2px;
          left: 0;
          background-color: $color;
          transition: width $animation-time;
        }
      }
      .navbar-list-item:last-child {
        margin-right: 0;
      }
      .navbar-list-item:hover .navbar-list-link:after {
        width: 100%;
      }
      .navbar-item-has-dropmenu {
        position: relative;
        .navbar-dropmenu-outer {
          position: absolute;
          left: -100%;
          top: $height;
          background: $dropMenu-bg-color;
          width: $subMenu-width;
          padding-left: 0;
          margin-bottom: 0;
          padding: 15px 0;
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
          transform-origin: center top;
          transition: all $animation-time $animation-type;
          transform: scaleY(0);
          z-index: 2000;
          list-style: none;
          font-size: $font-size * 0.9;
          li {
            padding: $margin-between-links * 0.4;
            text-align: left;
            position: relative;
            transition: all $animation-time $animation-type;
            a {
              text-decoration: none;
              color: $color;
            }
            .navbar-dropmenu-inner {
              position: absolute;
              left: calc(100% + 1px);
              top: -25%;
              background: $dropMenu-bg-color;
              width: $subMenu-width;
              padding-left: 0;
              margin-bottom: 0;
              padding: ($margin-between-links * 0.6) 0;
              box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
              transform: scaleX(0);
              transform-origin: center left;
              z-index: 3000;
              list-style: none;
            }
            &:before {
              content: "";
              display: block;
              height: 1px;
              width: 0;
              background-color: $color;
              position: absolute;
              top: 50%;
              left: 0;
              transition: width $animation-time $animation-type;
            }
            &:hover {
              padding-left: 15px;
              &:before {
                width: 10px;
              }
              .navbar-dropmenu-inner {
                transform: scaleX(1);
                transition: all ($animation-time * 1.4) $animation-type;
              }
            }
          }
        }
      }
      .navbar-item-has-dropmenu:hover {
        .navbar-dropmenu-outer {
          transform: none;
          transition: all ($animation-time * 1.4) $animation-type;
        }
      }
    }
  }
  @media (max-width: 992px) {
    .navbar-container {
      height: $height - 0.15 * $height;
      padding: 0 $padding/2;
      .navbar-brand {
        .navbar-brand-title {
          font-size: $font-size * 1.7;
        }
      }
      .navbar-list {
        .navbar-list-item {
          margin: 0 $margin-between-links/2;
        }
        .navbar-item-has-dropmenu {
          .navbar-dropmenu-outer {
            top: $height - 0.15 * $height;
            width: $subMenu-width * 0.85;
            li {
              .navbar-dropmenu-inner {
                width: $subMenu-width * 0.75;
              }
            }
          }
        }
      }
    }
  }
  @media (max-width: 768px) {
    .navbar-container {
      height: $height - 0.35 * $height;
      padding: 0 $padding/4;
      background-color: $mobile-color;
      border: none;
      .navbar-switch {
        display: block;
        font-size: $font-size * 1.75;
        display: flex;
        justify-content: center;
        flex-direction: column;
        cursor: pointer;
      }
      .navbar-brand {
        .navbar-brand-title {
          font-size: $font-size * 1.75;
        }
      }
      .navbar-list {
        transition: all $animation-time $animation-type;
        transform-origin: center top;
        transform: scaleY(0);
        position: absolute;
        top: 100%;
        left: 0;
        flex-direction: column;
        background-color: $mobile-color;
        width: 100%;
        .navbar-list-item {
          margin: 0 $padding/4;
          padding: 8px 0 8px 20px;
          display: block;
          text-align: left;
          background-color: $color;
          border: 1px solid #ddd;
          border-collapse: collapse;
          .navbar-list-link {
            font-size: $font-size * 0.9;
            font-weight: $font-weight;
            color: #565656;
          }
          .navbar-list-link:after {
            display: none;
          }
          &:hover {
            background-color: $dropMenu-bg-color;
            .navbar-list-link {
              color: white;
            }
          }
        }
        .navbar-list-item:last-child {
          margin-right: $padding/4;
        }
        .navbar-item-has-dropmenu {
          .navbar-dropmenu-outer {
            left: auto;
            right: 0;
            top: 0 !important;
            background: $dropMenu-bg-color;
            width: $subMenu-width * 0.8;
            li {
              a {
                color: $mobile-color;
              }
              .navbar-dropmenu-inner {
                transform-origin: right center;
                right: 100%;
                left: auto;
                width: $subMenu-width * 0.75;
              }
            }
          }
        }
      }
    }
  }
}
</style>
