<template>
  <section class="quotes-slider" v-if="sliderData.length > 0">
    <div class="quotes-container">
      <img
        :src="sliderData[activeSlide].authorImage"
        alt="author image"
        id="image"
        class="author-image"
      />
      <div id="quotes">
        <p class="content">
          {{ sliderData[activeSlide].quotesContent }}
        </p>
        <h5 class="author">
          <span class="name">
            {{ sliderData[activeSlide].author }}
          </span>

          <span class="position">{{
            sliderData[activeSlide].authorPosition
          }}</span>
        </h5>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "QuotesSlider",
  data() {
    return {
      sliderData: [],
      activeSlide: 0,
      slideTime: 10000
    };
  },
  methods: {
    ...mapMutations(["stopUpdateQuotes"]),
    async fetchData() {
      await this.stopUpdateQuotes();
      let response = null;
      if (this.activeLang == "en") {
        response = await axios.get("json/quotes.json");
      } else if (this.activeLang == "ar") {
        response = await axios.get("json/quotes_ar.json");
      }
      this.sliderData = response.data;
    }
  },
  computed: mapGetters(["updateQuotes", "activeLang"]),
  updated() {
    if (this.updateQuotes === 0) {
      this.stopUpdateQuotes();
      this.fetchData();
    }
  },
  created() {
    this.fetchData();
    setInterval(() => {
      let index = this.activeSlide + 1;
      if (index === this.sliderData.length) {
        index = 0;
      }
      const heading = document.getElementById("quotes");
      if (heading !== null) {
        const index = heading.className.indexOf("animate-text");
        if (index === -1) {
          const classString = heading.className;
          const newClass = classString.concat(" animate-text");
          heading.className = newClass;
          setTimeout(function() {
            const index = heading.className.indexOf("animate-text");
            const newCLass = heading.className.substring(0, index - 1);
            heading.className = newClass;
          }, this.slideTime / 2);
        } else {
          const newClass = heading.className.substring(0, index - 1);
          heading.className = newClass;
          setTimeout(function() {
            const classString = heading.className;
            const newCLass = classString.concat(" animate-text");
            heading.className = newCLass;
          }, 50);
        }
      }
      const main = document.getElementById("image");
      if (main !== null) {
        const index = main.className.indexOf("flashEffect");
        if (index === -1) {
          const classString = main.className;
          const newClass = classString.concat(" flashEffect");
          main.className = newClass;
          setTimeout(() => {
            const index = main.className.indexOf("flashEffect");
            const newClass = main.className.substring(0, index - 1);
            main.className = newClass;
          }, (this.slideTime * 1000) / this.slideTime);
        }
      }
      this.activeSlide = index;
    }, this.slideTime);
  }
};
</script>

<style lang="scss" scoped>
.quotes-slider {
  width: 100%;
  height: 100%;
  user-select: none;
  .quotes-container {
    width: calc(100% - 80px);
    height: 100%;
    margin: 70px 40px;
    padding: 70px 40px;
    position: relative;
    box-shadow: 0 0 20px rgba(42, 104, 252, 0.1);
    display: flex;
    flex-direction: column;
    &:after,
    &:before {
      font-family: "FontAwesome";
      font-size: 5rem;
      color: #333;
      opacity: 0.4;
      position: absolute;
    }
    &:before {
      top: 0;
      left: 0;
      content: "\f10d";
    }
    &:after {
      bottom: 0;
      right: 0;
      content: "\f10e";
    }
    .author-image {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      align-self: center;
    }
    .flashEffect {
      animation: flash 1 0.4s forwards;
    }
    @keyframes flash {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
    .content {
      font-size: 1.5rem;
      font-weight: 300;
      font-style: italic;
      color: #666666;
      line-height: 42px;
      margin: 25px;
      text-align: center;
    }
    .author {
      text-align: center;
      color: #333;
      .name {
        display: inline-block;
        margin: auto;
        padding: 5px 15px;
        font-size: 1.5rem;
        position: relative;
        background: #fff;
        &:after {
          content: "";
          display: block;
          position: absolute;
          top: 50%;
          left: -25%;
          width: 150%;
          height: 3px;
          background: #333;
          opacity: 0.2;
          z-index: -1;
        }
      }
      .position {
        display: block;
        font-size: 1rem;
        font-weight: 200;
        margin-top: 7px;
      }
    }
    .animate-text {
      animation: animateText 0.5s 1 ease-in-out forwards;
    }
    @keyframes animateText {
      0% {
        transform: scale(0);
      }
      100% {
        transform: scale(1);
      }
    }
  }
}
/*==========================================================================
============ Medium Screen =================
===========================================*/
@media (max-width: 768px) {
  .quotes-slider {
    .quotes-container {
      width: calc(100% - 40px);
      margin: 40px 20px;
      padding: 40px 20px;
      &:after,
      &:before {
        font-size: 3rem;
      }
      .content {
        font-size: 1.2rem;
        font-weight: 200;
        line-height: 35px;
        margin: 15px;
      }
      .author {
        .name {
          padding: 2px 1px;
          font-size: 1.2rem;
        }
        .position {
          font-size: 0.9rem;
          font-weight: 200;
          margin-top: 7px;
        }
      }
    }
  }
}
/*==========================================================================
============ Small Screen =================
===========================================*/
@media (max-width: 576px) {
  .quotes-slider {
    .quotes-container {
      width: calc(100% - 20px);
      margin: 20px 10px;
      padding: 20px 10px;
      &:after,
      &:before {
        font-size: 1.5rem;
      }
      .content {
        font-size: 1rem;
        font-weight: 200;
        line-height: 25px;
        margin: 5px;
      }
      .author {
        .name {
          padding: 2px 1px;
          font-size: 0.9rem;
        }
        .position {
          font-size: 0.8rem;
        }
      }
    }
  }
}
</style>
