import axios from "axios";

const state = {
  navbarItems: [],
  footerData: []
};

const getters = {
  navbarItems: state => state.navbarItems,
  footerData: state => state.footerData
};

const actions = {
  fetchNavbarItems: ({ rootState, commit }) => {
    let data = null;
    let footerData = null;
    if (rootState.lang.activeLang === "en") {
      data = axios.get("json/navbar.json");
      footerData = axios.get("json/footer.json");
    } else if (rootState.lang.activeLang === "ar") {
      data = axios.get("json/navbar_ar.json");
      footerData = axios.get("json/footer_ar.json");
    }
    data
      .then(response => commit("setNavbarItems", response.data))
      .catch(err => console.log(err));
    footerData
      .then(response => commit("setFooterData", response.data))
      .catch(err => console.log(err));
  }
};

const mutations = {
  setNavbarItems: (state, data) => (state.navbarItems = data),
  setFooterData: (state, data) => (state.footerData = data)
};

export default {
  state,
  getters,
  actions,
  mutations
};
