<template>
  <div class="home">
    <Slider></Slider>
    <WhoWeAre></WhoWeAre>
    <WhatWeDo></WhatWeDo>
    <Skills></Skills>
    <Tabs></Tabs>
    <Separator></Separator>
    <QuotesSlider></QuotesSlider>
    <SlideSlider></SlideSlider>
    <Footer></Footer>
    <ScrollToTop></ScrollToTop>
  </div>
</template>

<script>
import Slider from "@/components/Slider";
import WhoWeAre from "@/components/WhoWeAre";
import WhatWeDo from "@/components/WhatWeDo";
import Skills from "@/components/Skills";
import Tabs from "@/components/Tabs";
import Separator from "@/components/Separator";
import QuotesSlider from "@/components/QuotesSlider";
import SlideSlider from "@/components/SlideSlider";
import Footer from "@/components/Footer";
import ScrollToTop from "@/components/ScrollToTop";

import { mapActions } from "vuex";
export default {
  name: "home",
  components: {
    Slider,
    WhoWeAre,
    WhatWeDo,
    Skills,
    Tabs,
    Separator,
    QuotesSlider,
    SlideSlider,
    Footer,
    ScrollToTop
  },
  methods: mapActions(["startSlider"]),
  created() {
    this.startSlider();
  },
  mounted() {
    const navbar = document.getElementById("mainNavbar");
    window.addEventListener("scroll", function() {
      if (window.pageYOffset > window.innerHeight * 0.75) {
        const classString = navbar.className;
        const index = classString.indexOf("fixed-navbar");
        if (index === -1) {
          const newClass = classString.concat(" fixed-navbar");
          navbar.className = newClass;
        }
      } else {
        let classString = navbar.className;
        const index = classString.indexOf("fixed-navbar");
        const newClass = classString.substring(0, index - 1);
        navbar.className = newClass;
      }
    });
  }
};
</script>
