<template>
  <section class="slider" v-if="sliderData.data">
    <div class="slider-container">
      <div
        id="mainImg"
        class="main-image"
        :style="{
          backgroundImage: `url(${sliderData.data[activeSlide].url}.png)`
        }"
      >
        <span
          id="next"
          class="next fa fa-chevron-right"
          @click="nextSlide()"
        ></span>
        <span class="prev fa fa-chevron-left" @click="prevSlide()"></span>
      </div>
      <div
        id="titles"
        @mousemove="handleMouseHover"
        @mouseout="handleMouseOut"
        :style="{
          transform: `translate(-50%, -50%) rotateX(${pageX}deg) rotateY(${pageY}deg)`
        }"
      >
        <h2 class="title" v-html="sliderData.data[activeSlide].heading"></h2>
        <h5
          class="sub-title"
          v-html="sliderData.data[activeSlide].subTitle"
        ></h5>
        <p class="desc">{{ sliderData.data[activeSlide].desc }}</p>
        <div>
          <router-link :to="sliderData.data[activeSlide].btnHref">
            <!-- <img
              :src="`/${sliderData.data[activeSlide].btnImage}.png`"
              alt="btn"
              class="btn-icon"
            /> -->
            <span class="btn-title">{{
              sliderData.data[activeSlide].btnTitle
            }}</span>
          </router-link>
        </div>
      </div>
    </div>
    <ul
      class="thumbnails"
      :class="{
        'left-dir': direction === 'ltr',
        'right-dir': direction === 'rtl'
      }"
    >
      <li
        class="thumb"
        v-for="(slide, index) in sliderData.data"
        :key="slide.id"
        :style="{ backgroundImage: `url('${slide.url}.png')` }"
        :class="{ active: index === activeSlide }"
        @click="chooseSlide(slide)"
      ></li>
    </ul>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "slider",
  data() {
    return {
      pageX: "",
      pageY: ""
    };
  },
  computed: mapGetters(["update", "direction", "sliderData", "activeSlide"]),
  updated() {
    if (this.update) {
      this.getSliderData();
    }
  },
  methods: {
    ...mapActions(["chooseSlide", "nextSlide", "prevSlide", "getSliderData"]),
    handleMouseHover: function(e) {
      const rotateRate = 100;
      if (
        e.pageX === window.innerWidth / 2 ||
        e.pageY === window.innerHeight / 2
      ) {
        this.pageX = 0;
        this.pageY = 0;
      } else if (e.pageX <= window.innerWidth / 2) {
        this.pageX = e.pageX / rotateRate;
        this.pageY = -e.pageY / rotateRate;
      } else if (e.pageY <= window.innerHeight / 2) {
        this.pageX = -e.pageX / rotateRate;
        this.pageY = e.pageY / rotateRate;
      } else {
        this.pageX = e.pageX / rotateRate;
        this.pageY = e.pageY / rotateRate;
      }
    },
    handleMouseOut: function() {
      this.pageX = 0;
      this.pageY = 0;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../general-settings";
$text-width: 50%; //max-width: 50%
$overlay-bg: rgba(0, 0, 0, 0.7);
$flash-low-opacity: 0.4;
$flash-high-opacity: 0.4;
$flash-duration: 1s;
$navigate-button-width: 50px;
$navigate-button-height: 75px;
$navigate-button-font-size: 35px;
$navigate-button-bg-color: $main-accessories-color;
$navigate-button-color: black;
$navigate-button-hover-color: #eee;
.slider {
  width: 100%;
  height: 100%;
  user-select: none;
  position: relative;
  .slider-container {
    width: 100%;
    height: 100%;
    position: relative;
    .main-image {
      height: 100vh;
      background-size: cover;
      background-position: bottom;
      background-repeat: no-repeat;
      position: relative;
    }
    .main-image:before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: $overlay-bg;
    }
    .main-image-rotate {
      transform: rotateY(180deg);
    }
    .flashEffect {
      animation: flash 1 $flash-duration forwards;
    }
    @keyframes flash {
      0% {
        opacity: $flash-low-opacity;
      }
      100% {
        opacity: $flash-high-opacity;
      }
    }
    .next,
    .prev {
      position: absolute;
      display: none;
      font-size: $navigate-button-font-size;
      width: $navigate-button-width;
      height: $navigate-button-height;
      background: $navigate-button-bg-color;
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
      line-height: 75px;
      opacity: 0.4;
      cursor: pointer;
      transition: all 0.4s;
      &:hover {
        color: $navigate-button-hover-color;
        opacity: 0.9;
      }
    }
    .next {
      right: 0;
    }
    .prev {
      left: 0;
    }
    &:hover {
      .next,
      .prev {
        display: block;
      }
    }
    #titles {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      width: $text-width;
      padding: 25px;
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: white;
      text-align: center;
      perspective: 1200px;
      transition: all 0.4s;
      will-change: transform;
    }
    .animate-text {
      animation: slideToUp 1s 1 ease-in-out forwards;
    }
    @keyframes slideToUp {
      0% {
        transform: translate(-50%, -45%);
      }
      100% {
        transform: translate(-50%, -50%);
      }
    }
    .title {
      font-family: sans-serif;
      font-size: 4rem;
    }
    .sub-title {
      padding-top: 15px;
      font-family: sans-serif;
    }
    .desc {
      margin-top: 15px;
      font-size: 1.2rem;
    }
    .btn-icon {
      height: 50px;
      margin: 0 10px;
      animation: flasher 10s infinite linear;
    }
    @keyframes flasher {
      0% {
        filter: brightness(1);
      }
      65% {
        filter: brightness(1);
        transform: scale(1);
      }
      70% {
        filter: brightness(1.2);
        transform: scale(1.1);
      }
      75% {
        filter: brightness(1);
        transform: scale(1);
      }
      82% {
        filter: brightness(1.4);
        transform: scale(1.1);
      }
      90% {
        filter: brightness(1);
        transform: scale(1);
      }
    }
    .btn-title {
      color: #333;
      background-color: white;
      font-weight: bold;
      display: inline-block;
      margin: 25px;
      padding: 7px 25px;
      position: relative;
      transition: all 0.4s;
      &:before,
      &:after {
        content: "";
        display: block;
        background: darkblue;
        width: 100%;
        height: 0;
        position: absolute;
        left: 0;
        transition: all 0.4s;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
      &:hover {
        color: white;
        background-color: transparent;
        transition: all 0.4s;
        &:before,
        &:after {
          height: 50%;
          transition: all 0.4s;
          z-index: -1;
        }
      }
    }
  }
  .thumbnails {
    position: absolute;
    list-style: none;
    width: 250px;
    height: 50px;
    display: flex;
    justify-content: center;
    padding: 0;
    bottom: 4%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    .thumb {
      width: 40px;
      height: 5px;
      // border-radius: 50%;
      background-size: cover;
      cursor: pointer;
      margin: 10px 3px;
      &:hover {
        filter: brightness(1.2);
      }
    }
    .thumb.active {
      filter: brightness(1.3);
      box-shadow: 1px 1px 15px;
    }
    .left-dir {
      left: 5%;
    }
    .right-dir {
      right: 5%;
    }
  }
}
/*======================================================================================
========= Medium Screen =============
====================================*/
@media (max-width: 992px) {
  .slider {
    .slider-container {
      .next,
      .prev {
        font-size: $navigate-button-font-size * 0.95;
        width: $navigate-button-width * 0.95;
        height: $navigate-button-height * 0.95;
      }
      #titles {
        width: $text-width * 1.5;
        padding: 15px;
      }
      .title {
        font-size: 3.4rem;
      }
      .sub-title {
        padding-top: 15px;
      }
      .desc {
        margin-top: 15px;
        font-size: 1rem;
        .btn-title {
          margin: 25px;
          padding: 7px 25px;
        }
      }
    }
  }
}
/*======================================================================================
========= Small Screen =============
====================================*/
@media (max-width: 576px) {
  .slider {
    .slider-container {
      .next,
      .prev {
        font-size: $navigate-button-font-size * 0.5;
        width: $navigate-button-width * 0.5;
        height: $navigate-button-height * 0.5;
        line-height: 40px;
      }
      #titles {
        width: $text-width * 1.75;
        padding: 15px;
      }
      .title {
        font-size: 1.5rem;
      }
      .sub-title {
        padding-top: 25px;
      }
      .desc {
        margin-top: 25px;
        font-size: 1rem;
        .btn-title {
          margin: 35px;
          padding: 7px 25px;
        }
      }
    }
  }
}
</style>
